import React from "react";
import { randomS4 } from "../functions";


export default class FullHeightTemplate extends React.Component {
  id = this.constructor.name + '-' + randomS4();
  render() {
    let className = [
      this.constructor.name,
      'd-flex flex-column h-100 w-100'
    ].join(' ');

    return <div className={className} id={this.id}
      style={{ position: 'absolute' }}>
      {this.props.children}
    </div>
  }
}